<template>
  <div>
    <span v-if="label" class="tag-style" :style="{ width: spanWidth }">{{
      label
    }}</span>
  </div>
</template>
<script>
export default {
  name: "Tag",
  props: {
    label: {
      type: String,
      default() {
        return "";
      }
    }
  },
  data() {
    return {
      spanWidth: "60px"
    };
  },
  created() {
    this.spanWidth = this.getTextWidth(this.label) + 30 + "px";
  },
  methods: {
    getTextWidth(text, font) {
      var canvas =
        this.getTextWidth.canvas ||
        (this.getTextWidth.canvas = document.createElement("canvas"));
      var context = canvas.getContext("2d");
      context.font = font;
      var metrics = context.measureText(text);
      return metrics.width;
    }
  }
};
</script>
<style scoped>
.tag-style {
  display: inline-block;
  color: rgb(245 149 6);
  padding: 0 5px;
  border-radius: 8px;
  border: 1px solid;
  white-space: nowrap;
  line-height: 16px;
}
</style>
